@font-face {
  font-family: "Industry";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/Industry-BoldItalic.woff");
}
@font-face {
  font-family: "IndustryMedium";
  font-style: normal;
  src: url("../fonts/Industry-Medium.ttf");
}
@font-face {
  font-family: "RobotoRegular";
  font-style: normal;
  src: url("../fonts/Roboto-Regular.ttf");
}
@font-face {
  font-family: "RobotoBold";
  font-style: normal;
  src: url("../fonts/Roboto-Bold.ttf");
}
h2, p, h5, h1, h3, h4 {
  margin: 0;
  padding: 0;
}

button {
  background-color: transparent;
  outline: 0;
  border: 0;
}

body {
  font-family: RobotoRegular;
}

.red-btn-def-wrap {
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.red-btn-def-wrap .red-btn-default {
  border-radius: 20px;
  height: 40px;
  width: 200px;
  background-color: #ce3426;
  color: #fff;
  font-weight: bold;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  font-size: 15px;
  -webkit-transition: opacity 0.2s;
  transition: opacity 0.2s;
}
.red-btn-def-wrap .red-btn-default:hover {
  opacity: 0.9;
  text-decoration: none;
  color: #fff;
}
.red-btn-def-wrap .red-btn-default:active {
  opacity: 1;
}

.deftitle-sec-h3 {
  font-size: 27px;
  font-family: RobotoBold;
  color: #11e5ef;
}

.padding-y {
  padding-top: 60px;
  padding-bottom: 60px;
}

.profile-container {
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: normal;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}
.profile-container .hide-profile {
  display: none;
}

.profile-cont-upbar {
  width: 100%;
  min-height: 55px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  background-color: #031328;
}
.profile-cont-upbar .container {
  min-height: 55px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.profile-cont-upbar .container span {
  font-size: 18px;
  height: 55px;
  width: 130px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  color: #11e5ef;
  font-family: RobotoBold;
  margin-right: 60px;
  cursor: pointer;
  position: relative;
}
.profile-cont-upbar .container span:hover {
  color: #ce3426;
}
.profile-cont-upbar .active {
  color: #ce3426 !important;
}
.profile-cont-upbar .active:after {
  content: "";
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 0;
  background-color: #074d5f;
  left: 0;
  border-top-left-radius: 50px;
  border-top-right-radius: 50px;
  -webkit-animation: active-hight-navprofile-anim 0.2s ease-in forwards;
          animation: active-hight-navprofile-anim 0.2s ease-in forwards;
}
@-webkit-keyframes active-hight-navprofile-anim {
  from {
    height: 0;
  }
  to {
    height: 5px;
  }
}
@keyframes active-hight-navprofile-anim {
  from {
    height: 0;
  }
  to {
    height: 5px;
  }
}

.profile-cont-wrapper,
.achiev-cont-wrapper {
  min-height: 400px;
  background-color: #074d5f;
  padding: 100px 0 80px 0;
}
.profile-cont-wrapper .container,
.achiev-cont-wrapper .container {
  padding: 0;
}

.profilecont-profile-container {
  min-height: 400px;
  width: 100%;
  min-height: 500px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}

.profc-profilecont-halfone,
.profc-profilecont-halftwo {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  min-height: 400px;
}

.profc-profilecont-halfone {
  width: 60%;
}

.profc-profilecont-halftwo {
  width: 40%;
}

.profilecont-hone-useravatar {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  margin-bottom: 30px;
}
.profilecont-hone-useravatar i {
  font-size: 100px;
  color: #fff;
}
.profilecont-hone-useravatar h4 {
  color: #fff;
  font-weight: bold;
  font-size: 24px;
}
.profilecont-hone-useravatar h4 span {
  color: #ce3426;
}

.profile-stitle-h4 {
  color: #fff;
  font-size: 20px;
  font-family: RobotoBold;
  margin-top: 40px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.profile-stitle-h4 i {
  font-size: 20px;
  margin-right: 10px;
  color: #ce3426;
}

.profile-sp-p {
  color: #fff;
  font-size: 16px;
  margin-top: 5px;
  width: 90%;
}
.profile-sp-p a {
  font-weight: bold;
  color: inherit;
}
.profile-sp-p a:hover {
  text-decoration: none;
  opacity: 0.8;
}

.profile-form-default {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}
.profile-form-default button {
  border: 1px solid #fff;
  width: 150px;
  height: 35px;
  margin-top: 20px;
  border-radius: 50px;
  background-color: #ce3426;
  outline: 0;
  border: 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  font-family: RobotoBold;
  color: #fff;
  -webkit-transition: opacity 0.2s;
  transition: opacity 0.2s;
}
.profile-form-default button:hover {
  opacity: 0.9;
}
.profile-form-default button:active {
  -webkit-box-shadow: 0 0 2px 0 #000;
          box-shadow: 0 0 2px 0 #000;
  opacity: 1;
}

.profile-form-inputs {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin-top: 40px;
}
.profile-form-inputs input {
  background-color: transparent;
  border: 0;
  outline: 0;
  border-bottom: 1px solid #11e5ef;
  margin-right: 60px;
  height: 35px;
  width: 40%;
  padding: 0 8px 0 8px;
  color: #fff;
}

.profc-profilecont-halftwo {
  position: relative;
  padding-top: 100px;
}
.profc-profilecont-halftwo img {
  max-width: 100%;
  height: auto;
}

.achievements-profilecont-halftwo {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  padding-top: 200px;
}
.achievements-profilecont-halftwo .bi-trophy-fill {
  color: #11e5ef;
  font-size: 250px;
}

@media (max-width: 1200px) {
  .profilecont-profile-container {
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
  }
  .profc-profilecont-halftwo {
    display: none;
  }
  .profc-profilecont-halfone {
    width: 100%;
  }
  .profile-cont-wrapper .container, .achiev-cont-wrapper .container {
    padding: 15px;
  }
  .profile-sp-p {
    width: 100%;
  }
}
@media (max-width: 767.5px) {
  .profile-form-inputs {
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
  }
  .profile-form-inputs input {
    margin: 10px 0 10px 0;
    max-width: 100%;
    width: 300px;
  }
}
@media (max-width: 576px) {
  .profile-cont-upbar .container {
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
  }
  .profile-cont-upbar .container span {
    width: 40%;
    margin-right: 0;
  }
}